<template lang="pug">
    .new__scholar-content
        .scholar__title Ученик
        form(@submit.prevent="saveScholar").new__scholar
            .new__scholar-points
                .form__row
                    .form__item
                        img(src='../../../assets/images/icons/cat.svg').img
                    .form__item
                        label.label__name Имя
                            input(type='text' v-model='scholar.firstName').new__scholar__name
                    .form__item
                        label.label__name Фамилия
                            input(type='text' v-model='scholar.lastName').new__scholar__lastname
                    .form__item
                        label.label__name ИИН
                            input(type='text' v-model='scholar.username').new__scholar__iin
                .form__row
                    .form__item
                        label.label__name Школа
                            input(v-model="scholar.scholarSchool").new__scholar__school
                    .form__item
                        label.label__name Класс
                            select(v-model="scholar.scholarClass").new__scholar__class
                                option(value=0) Выбрать
                                option(:value='item' v-for='item in 12') {{item}}
                .form__row
                    button(type='submit').save__button-scholar Сохранить
                    button(@click.prevent='resetScholar').reset__button-scholar Отмена
            
</template>
<script>
import { mapActions } from "vuex";
export default {
    data() {
        return {
            scholar: {
                firstName: '',
                lastName: '',
                username: '',
                scholarClass: 0,
                scholarSchool: '',
                password: 'qwerty'
            }
        }
    },
    methods: {
        ...mapActions("scholars", ["addScholar"]),
        ...mapActions("error", ["fetchError"]),
        saveScholar() {
            if(this.scholar.firstName==''||
                this.scholar.lastName==''||
                this.scholar.username==''||
                this.scholar.scholarClass ==0||
                this.scholar.scholarSchool==''){
                this.fetchError('Не все поля заполнены!')
            }
            else{
                this.addScholar(this.scholar)
                this.$emit('eventNewScholar')
            }
        },
        resetScholar() {
            this.$emit('eventNewScholar')
        }
    }
}
</script>
